.header {
  display: flex;
  justify-content: center;
  align-items: center;
}
.slider {
  display: flex;
  flex-direction: column;
  align-items: center;

  padding: 0 16px;
  max-width: 598px;
  width: 100%;

  @media (max-width: 768px) {
    & {
      max-width: 300px;
    }
  }

  &__title {
    font-family: "Helvetica", Sans-Serif;
    font-style: normal;
    font-weight: 300;
    font-size: 72px;
    line-height: 83px;
    text-align: center;

    color: #f2f4f7;

    margin-bottom: 32px;

    @media (max-width: 1024px) {
      & {
        font-size: 60px;
      }
    }
    @media (max-width: 768px) {
      & {
        font-size: 40px;
        line-height: 50px;
      }
    }
  }

  &__text {
    font-family: "Helvetica";
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    color: #f2f4f7;

    margin-bottom: 80px;

    @media (max-width: 1024px) {
      & {
        font-size: 16px;
      }
    }
  }

  &__button {
    padding: 16px 96px;
    margin-bottom: 64px;

    border: 2px solid;
    border-image: linear-gradient(92.82deg, #fc4060 0%, #8061fd 100%);
    border-image-slice: 1;
    background: linear-gradient(92.82deg, #fc4060 35%, #8061fd 70%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    font-family: "Helvetica", Sans-Serif;
    font-style: normal;
    font-weight: 400;
    font-size: 1.1rem;
    line-height: 21px;
  }

  &__list {
    display: flex;
    flex-direction: row;

    padding: 0 16px;
    margin-bottom: 229px;
  }

  &__eclipse {
    border: 2px solid rgba(196, 196, 196, 0.16);
    border-radius: 50%;

    width: 16px;
    height: 16px;
    margin-right: 32px;

    @media (max-width: 768px) {
      & {
        width: 10px;
        height: 10px;
      }
    }

    &:last-child {
      margin-right: 0;
    }

    &--active {
      border: 2px solid #ffffff;
    }
  }
}
