.app {
  max-width: 1410px;
  width: 100%;
  margin: 95px auto;

  background: linear-gradient(
    180deg,
    rgba(43, 34, 21, 0.8) -1.22%,
    #22282e 101.33%
  );
  box-shadow: 0px 0px 250px rgba(0, 0, 0, 0.3);
  border-radius: 32px;

  @media (max-width: 1024px) {
    & {
      max-width: 950px;
    }
  }
  @media (max-width: 768px) {
    & {
      max-width: 500px;
    }
  }
}
